

import BScroll from "better-scroll"
export default {
name: "approve",
data () {
return {
data: [0,1,2,3,4,5,6],
pulldownMsg: '下拉刷新',
pullupMsg: '加载更多',
alertHook: 'none',
cardount:'',
tableData: [], //工作机会数据
itemList:[
{
'title':''
},
],
count:'1',
datas:[],
sildename:'slide-back',
}
},
methods:{
data2() {
this.datas = [[
{
mc:'思维逻辑',
dj:'5',
bz:'0',
colors:'#4bec78'
},
{
mc:'思维逻辑2',
dj:'6',
bz:'5',
colors:'#4bec78'
},
{
mc:'思维逻辑2',
dj:'4',
bz:'6',
colors:'#4bec78'
},
],[
{
mc:'团队配合',
dj:'7',
bz:'3',
colors:'#ec4bea'
},
{
mc:'团队配合2',
dj:'7',
bz:'3',
colors:'#ec4bea'
},
{
mc:'团队配合3',
dj:'7',
bz:'3',
colors:'#ec4bea'
}
],[
{
mc:'归纳总结',
dj:'9',
bz:'9',
colors:'#32dea8'
},{
mc:'归纳总结2',
dj:'9',
bz:'9',
colors:'#32dea8'
},{
mc:'归纳总结3',
dj:'9',
bz:'9',
colors:'#32dea8'
}
],

]
console.log(this.datas)
},
getData() {
return new Promise(resolve => {  //模拟数据请求
setTimeout(() => {
const arr = [];
for (let i = 0; i < 20; i++) {
arr.push('1')
}
resolve(arr)
}, 1000)
})
},
getJob(){
this.$fetch('Job/special').then((res)=>{
console.log(res.data)
this.tableData = res.data
this.cardount = (this.tableData.length)*250+(this.tableData.length)*10+(10)  
console.log(this.cardount)
}).catch((response)=>{
console.log(response);
})
},
ceshi(id){
console.log(id)
},
refreshalert(){   //刷新成功提示
this.alertHook = 'block';
setTimeout(()=>{
this.alertHook = 'none'
},1000)

},

InitTabScroll(){
let width=0
for (let  i = 0; i <this.itemList.length; i++) {
width+=this.$refs.tabitem1[0].getBoundingClientRect().width
}
this.$refs.tabcontent1.style.width=width+'px'

this.$nextTick(()=>{
if (!this.scroll) {
this.scroll=new BScroll(this.$refs.tab1, {
startX:0,
click:true,
scrollX:true,
scrollY:false,
eventPassthrough:'vertical'
});
this.scroll=new BScroll(this.$refs.tab2, {
startX:0,
click:false,
scrollX:true,
scrollY:false,
eventPassthrough:'vertical'
})
}else{
this.scroll.refresh()
}
})
},
},
created(){

},
mounted () {
this.getJob();
this.data2();

const that = this;
let refreshed = false;
this.InitTabScroll()
this.$nextTick(() => {
this.scroll = new BScroll(this.$refs.wrapper,{
probeType:1,
click: true,
minScrollY :-40,
});
this.scroll.on('scroll',(pos)=>{

if(pos.y > 30){
this.pulldownMsg = '释放立即刷新'
this.scroll.minScrollY = 40;
refreshed = true;
}
});
this.scroll.on('scrollEnd',(pos)=>{
if(refreshed){
this.pulldownMsg = '正在刷新'
}
})

this.scroll.on('touchEnd',(pos) =>{  //上拉刷新
if(pos.y > 30){
setTimeout(()=>{
that.getData().then((res)=>{

that.data = res;

that.pulldownMsg = '下拉刷新'

that.refreshalert();

that.scroll.refresh();
refreshed = false;
})
},2000)
}
else if(pos.y<(this.scroll.maxScrollY - 30)){   //下拉加载
this.pullupMsg = '加载中。。。';
setTimeout(()=>{
that.getData().then((res)=>{

that.pullupMsg = '加载更多';
that.data = this.data.concat(res);
that.scroll.refresh();
})
},2000)

}
})
});
},
}



