import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Main from '@/components/Main'

//demo
import login from '@/pages/login'
import home from '@/pages/home'

import story from '@/pages/demo/story' //故事
import test from '@/pages/test'  //测试页面
import approve from '@/pages/demo/approve'  //认证页面

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path:'/',
      redirect:'approve'
    },
    {
      path: '/login',
      name: 'login',
      component: login,
    },{
      path: '/',
      name: 'Main',
      component: Main,
      children:[{
        path: '/home',
        name: 'home',
        component: home,
      }, {
        path: '/approve',
        name: 'approve',
        component: approve,
      }
        ,{
        path: '/story',
        name: 'story',
        component: story,
      }]
    },{
      path:'/test',
      name: 'test',
      component: test,
    }
  ]
})



