
import Loading from 'vux/src/components/loading/index.vue'

import { mapState } from 'vuex'
export default {
name: 'app',
data(){
return {
loadText:"loading"
}
},
components: {
Loading
},
computed:{
...mapState({
isLoading: state => state.myVux.isLoading
})
}
}



